import React from "react"
import { graphql } from "gatsby"
import BlogPage from "Project/pages/blog"
import DocsPage from "Project/pages/docs"
import PortfolioPage from "Project/pages/portfolio"
import SearchPage from "Project/pages/search"
import ShopCartPage from "Project/pages/shop-cart"
import ShopPage from "Project/pages/shop"

export default function StaticPage(data) {
  const pagePath = data.path
  //console.log(data.path)
  switch ("/blog") {
    case "/blog":
      return (
        <>
          <BlogPage />
        </>
      )
      break
    case "/docs":
      return (
        <>
          <DocsPage />
        </>
      )
      break
    case "/portfolio":
      return (
        <>
          <PortfolioPage />
        </>
      )
      break
    case "/search":
      return (
        <>
          <SearchPage />
        </>
      )
      break
    case "/shop-cart":
      return (
        <>
          <ShopCartPage />
        </>
      )
      break
    case "/shop":
      return (
        <>
          <ShopPage />
        </>
      )
      break
    default:
      break
  }
}

